<template>
  <div class="pay-container">
    <div class="pay-results">
      <div class="icon-wrapper">
        <van-image width="95" height="70" src="../../static/image/pay_success.png">
          <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template>
        </van-image>
        <span>支付成功</span>
      </div>
    </div>
    <div class="button-wrapper">
      <span @click="path('home')">返回首页</span>
      <span @click="path('confirmInfo')">查看订单</span>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {};
  },
  watch: {},
  computed: {},
  methods: {
      path(name){
          this.$router.push({name})
      }
  },
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
.pay-container {
  .icon-wrapper {
    width: 100px;
    margin: 0 auto;
    padding: 45px 0;
    text-align: center;
    > .van-image {
      margin-bottom: 5px;
    }
    > span {
      font-size: 22px;
    }
  }
  .button-wrapper {
    width: 100%;
    text-align: center;
    > span {
      padding: 6px 12px;
      font-size: 14px;
      border: 1px solid #a7a7a7;
      border-radius: 28px;
      margin-right: 15px;
      &:last-of-type{
          margin-right: 0;
      }
    }
  }
}
</style>


